/* src/App.css */
body {
  margin: 0;
  background-color: #0d0d0d;
  color: #00ff00;
  font-family: 'Courier New', Courier, monospace;
}

.container {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.warning {
  text-align: center;
  font-size: 1.5em;
  padding: 2em;
  color: #ff3333;
}

.left-panel {
  flex: 1;
  padding: 1em;
  background-color: #101010;
  border-right: 1px solid #333;
}

.right-panel {
  position: absolute;
  right: 0;
  top: 3em;
  width: 200px;
  padding: 1em;
  border-left: 1px solid #333;
  background-color: #101010;
}

.folder {
  margin-bottom: 0.5em;
  cursor: pointer;
}

.terminal {
  position: absolute;
  bottom: 0;
  width: 100%;
  background-color: #000000;
  padding: 1em;
  font-size: 0.9em;
  border-top: 1px solid #333;
}